* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.general-wrap {
  margin: 0px;
  height: auto;
}

.header {
  position: relative;
  height: auto;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.07)), url("../assets/images/cover-page4.jpg");
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 63% center;
}
.header .wrap-page {
  height: calc(100vh + 100px);
  width: 100%;
  position: relative;
}
.header .wrap-page .header__title {
  width: 90%;
  position: absolute;
  top: 35%;
  transform: translateY(-50%);
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-family: "Montserrat";
  font-size: 58px;
  color: white;
  text-shadow: 1px 1px 2px black;
  text-transform: uppercase;
}
.header .wrap-page .header__sub-title {
  width: 50%;
  position: absolute;
  top: 60%;
  transform: translateY(-50%);
  left: 50%;
  transform: translateX(-50%);
  font-family: "Montserrat";
  font-weight: 600;
  text-align: center;
  color: rgb(216, 216, 216);
  font-size: 42px;
}

@media (max-width: 1140px) {
  .header .wrap-page .header__title {
    font-size: 48px;
  }
  .header .wrap-page .header__sub-title {
    font-size: 36px;
  }
}
