.menu {
  display: flex;
  justify-content: space-between;
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  height: 70px;
  border-bottom: 1px solid rgba(196, 196, 196, 0.5);
  background-color: #2c2d34;
  transition: all 500ms;
}
.menu-phone {
  display: none;
}
.menu__logo {
  width: 60px;
  margin: 10px 0px 3px 15px;
}
.menu__logo a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.menu__logo a img {
  max-width: 100%;
  max-height: 100%;
  padding-bottom: 5px;
}
.menu__categories {
  width: 55%;
  margin-right: 50px;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
}
.main-nav__categories {
  display: block;
  position: relative;
  list-style: none;
  font-family: "Oswald";
  font-weight: 700;
}
.main-nav__categories a {
  display: block;
  text-decoration: none;
  color: white;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 1.2px;
  transition: 350ms;
  padding: 5px 5px;
}
.main-nav__categories .webSymbols {
  cursor: pointer;
  font-family: "WebSymbols";
  font-size: 16px;
}
.main-nav__categories .icon-svg {
  width: 16px;
  cursor: pointer;
}
.main-nav__categories--sub-menu {
  position: relative;
  cursor: pointer;
}
.main-nav__categories--sub-menu > a {
  display: block;
  margin: 17px 0px;
}
.main-nav__categories a:hover {
  background-color: #055A81;
  text-shadow: 0px 0px 2px rgb(36, 36, 36);
}
.main-nav__categories--sub-menu:hover .sub-nav {
  display: block;
}
.main-nav__categories--icons a:hover {
  color: #055A81;
  background-color: unset;
  transform: scale(1.4);
  cursor: pointer;
}

.sub-nav {
  display: none;
  z-index: 2;
  position: absolute;
  width: 100%;
}
.sub-nav__categories {
  position: relative;
  width: 100%;
  list-style: none;
  text-align: center;
  border: 1px solid rgba(196, 196, 196, 0.5);
  background-color: #2c2d34;
}
.sub-nav__categories a {
  display: block;
  padding: 10px 0px;
  width: 100%;
  height: 100%;
}

.menuUp {
  transform: scaleY(108%);
  background-color: transparent !important;
}

.menuDown {
  transform: scaleY(100%);
  background-color: #2c2d34 !important;
}

.dropdown-menu {
  display: none;
}

.scroll {
  display: none;
  position: absolute;
}

@media (max-width: 1100px) {
  .menu__categories {
    margin-right: 25px;
    width: 70%;
  }
}
@media (max-width: 850px) {
  .menu__categories {
    margin-right: 10px;
    width: 85%;
  }
}
@media (max-width: 700px) {
  .menu__categories {
    width: 85%;
  }
  .main-nav__categories a {
    font-size: 11px;
    padding: 5px;
  }
  .main-nav__categories .webSymbols {
    font-size: 14px;
  }
  .main-nav__categories--sub-menu > a {
    margin: 19px 0px;
  }
}
@media (max-width: 600px) {
  .menu-phone {
    display: flex;
    flex-flow: wrap row;
    justify-content: space-evenly;
    align-items: center;
    position: fixed;
    z-index: 3;
    width: 100%;
    height: 70px;
    padding-top: 10px;
    background-color: #2c2d34;
    border-bottom: 1px solid rgba(0, 0, 0, 0.507);
    transition: all 0.5s;
    transform: none;
  }
  .menu-phone__logo {
    flex-basis: 30%;
    display: flex;
    flex-flow: wrap row;
    justify-content: center;
    align-items: center;
    height: 90%;
  }
  .menu-phone__logo a {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
  .menu-phone__logo a img {
    height: 90%;
  }
  .menu-phone__bars {
    flex-basis: 30%;
    display: flex;
    flex-flow: wrap row;
    justify-content: flex-start;
    align-items: center;
    height: 100%;
  }
  .menu-phone__bars i {
    cursor: pointer;
    font-size: 25px;
    color: white;
    margin-left: 20%;
    transition: all 0.8s;
  }
  .menu-phone__bars i:hover {
    color: rgb(226, 225, 225);
  }
  .menu-phone__modals {
    flex-basis: 30%;
    display: flex;
    flex-flow: wrap row;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
  }
  .menu-phone__modals i {
    color: white;
    font-size: 13px;
    cursor: pointer;
  }
  .menu-phone__modals i:hover {
    color: #055A81;
  }
  .menu {
    display: none;
  }
  .dropdown-menu {
    display: block;
    opacity: 0;
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: rgba(44, 45, 52, 0.9803921569);
    z-index: 2;
    transition: all 0.8s linear;
  }
  .dropdown-menu .main-nav--phone > ul {
    display: flex;
    flex-flow: wrap column;
    justify-content: space-evenly;
    align-items: flex-start;
    width: 80%;
    margin: 0px auto;
    padding-top: 100px;
  }
  .dropdown-menu .main-nav--phone > ul li {
    width: 100%;
    margin-bottom: 15px;
    border-bottom: 1px solid rgba(227, 228, 229, 0.7333333333);
  }
  .dropdown-menu .main-nav--phone > ul li a {
    font-weight: 400;
    font-size: 13px;
    margin: 0px;
  }
  .dropdown-menu .main-nav--phone > ul li:last-child {
    border-bottom: none;
  }
  .dropdown-menu .main-nav--phone > ul .sub-nav {
    position: relative;
    width: 90%;
    margin-left: auto;
    padding-top: 2px;
    transition: all 1s;
  }
  .dropdown-menu .main-nav--phone > ul .sub-nav__categories {
    margin-bottom: 5px;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #e3e4e5;
  }
  .dropdown-menu .main-nav--phone > ul .sub-nav__categories a {
    text-align: left;
  }
  .dropdown-menu .main-nav--phone > ul .main-nav__categories--sub-menu a:hover .sub-nav {
    display: block;
    position: static;
  }
  .show-dropdown {
    transform: translateY(0%);
    opacity: 1;
  }
  .hide-dropdown {
    transform: translateY(-200%);
    opacity: 0;
  }
  .scroll {
    z-index: 2;
    top: calc(100% + 17px);
    left: 0px;
    cursor: pointer;
    padding: 15px 15px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: #2c2d34;
  }
  .scroll i {
    color: white;
    font-size: 20px;
  }
}
@media (max-width: 280px) {
  .menu-phone__bars i {
    font-size: 23px;
  }
  .menu-phone__modals i {
    font-size: 12px;
  }
  .menu-phone__logo a img {
    height: 75%;
  }
  .menu-phone {
    padding-top: 0px;
  }
}
@media (max-width: 250px) {
  .dropdown-menu .main-nav--phone > ul li a {
    font-weight: 300;
    font-size: 11px;
  }
  .menu-phone__bars i {
    font-size: 17px;
  }
}
@media (max-width: 210px) {
  .menu-phone__logo a img {
    height: 65%;
  }
  .menu-phone {
    height: 65px;
  }
  .menu-phone__modals i {
    font-size: 10px;
  }
  .header .wrap-page {
    height: 50vh;
  }
  .header .wrap-page .header__title {
    top: 53%;
    font-size: 13px;
  }
  .header .wrap-page .header__sub-title {
    font-size: 11px;
  }
  .menu-phone {
    height: 55px;
  }
  .scroll {
    padding: 11px;
    top: calc(100% + 10px);
  }
  .scroll i {
    font-size: 15px;
